html {
  font-family: Roboto, Arial, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

body {
  background-color: #fff;
}

.stars {
  gap: 4px;
  list-style: none;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  transition-duration: .3s;
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 0 auto;
  display: block;
}

.card__title {
  color: #060b35;
  margin-top: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card:hover .card__title {
  color: #34568b;
}

.card__code {
  color: #616070;
  margin-top: 4px;
  font-size: 10px;
  line-height: 14px;
}

.card__rating {
  justify-content: space-between;
  align-items: center;
  margin-top: 16px;
  display: flex;
}

.card__reviews-count {
  color: #060b35;
  font-size: 10px;
}

.card__price {
  justify-content: space-between;
  margin-top: 24px;
  display: flex;
}

.card__price-text {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card__price-value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__buy-button {
  box-sizing: border-box;
  text-align: center;
  text-transform: uppercase;
  transition-duration: inherit;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  text-decoration: none;
  display: block;
}

.card__buy-button:link, .card__buy-button:visited {
  color: #fff;
  background: #00acdc;
}

.card__buy-button:hover, .card__buy-button:active {
  color: #00acdc;
  background: #fff;
  border: 1px solid #00acdc;
}

.card:hover {
  transition-duration: .3s;
  transform: scale(1.2);
}

.catalog {
  --catalog-column-count: 1;
  grid-template-columns: repeat(var(--catalog-column-count), 200px);
  place-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) and (width <= 767px) {
  .catalog {
    --catalog-column-count: 2;
  }
}

@media (width >= 768px) and (width <= 1023px) {
  .catalog {
    --catalog-column-count: 3;
  }
}

@media (width >= 1024px) {
  .catalog {
    --catalog-column-count: auto-fill;
  }
}

.logo-link {
  width: 40px;
  height: 40px;
  text-decoration: none;
  display: flex;
}

.nav__list {
  flex-wrap: wrap;
  place-content: center;
  gap: 20px;
  list-style-type: none;
  display: inline-flex;
}

.nav__list-item {
  display: inline;
}

.nav__link {
  text-align: center;
  text-transform: uppercase;
  height: 60px;
  line-height: 60px;
  text-decoration: none;
  transition-duration: .3s;
  display: inline-block;
  position: relative;
}

.nav__link:after {
  content: "";
  transition-duration: inherit;
  background: #00acdc;
  border-radius: 8px;
  width: 0%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 50%;
}

.nav__link:link, .nav__link:visited {
  color: #060b35;
}

.nav__link:hover, .nav__link:active {
  color: #00acdc;
}

.nav__link:active:after {
  width: 100%;
  left: 0;
}

.is-active:link, .is-active:visited {
  color: #00acdc;
}

.is-active:after {
  width: 100%;
  left: 0;
}

.header {
  background-color: #fff;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}
